.youtube-slider {
  margin: auto;
  padding-bottom: 100px;
}
.youtube-slider .ytp-more-videos-view,
.youtube-slider .ytp-pause-overlay {
  display: none !important;
}
.youtube-slider .slick-list {
  width: calc(100% + 40px);
}
.youtube-slider .youtube-slide {
  border-radius: 17px;
  margin-right: 20px;
  width: 100%;
  min-width: 300px;
  height: 535px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
  overflow: hidden;
}
.youtube-slider .youtube-wrapper {
  border-radius: 17px;
  width: 100%;
  max-width: 300px;
  height: 535px;
  position: relative;
  overflow: hidden;
}
.youtube-slider .youtube-wrapper button {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: white;
  border: 3px solid #fff;
  font-size: 24px;
  cursor: pointer;
  width: 56px;
  height: 56px;
  border-radius: 50%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.youtube-slider .youtube-wrapper .youtube-play {
  background: rgba(0, 0, 0, 0);
}
.youtube-slider .youtube-wrapper .youtube-pause {
  background: rgba(0, 0, 0, 0);
}
.youtube-slider .youtube-thumbnail,
.youtube-slider iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.youtube-slider .slick-slider {
  display: flex;
  flex-direction: column;
}
.youtube-slider .slick-prev,
.youtube-slider .slick-next {
  position: absolute;
  top: auto;
  bottom: 0;
  transform: translateY(-50%);
  width: 56px;
  height: 56px;
  border-radius: 50%;
  border: 2px solid #fff;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  z-index: 10;
}
.youtube-slider .slick-prev::before,
.youtube-slider .slick-next::before {
  content: "";
  display: inline-block;
  width: 21px;
  height: 21px;
  border: solid #fff;
  border-width: 0 3px 3px 0;
  opacity: 1;
}
.youtube-slider .slick-prev {
  right: 210px;
  left: auto;
}
.youtube-slider .slick-prev::before {
  margin-left: 6px;
  transform: rotate(135deg);
}
.youtube-slider .slick-next {
  right: 140px;
}
.youtube-slider .slick-next::before {
  margin-right: 6px;
  transform: rotate(-45deg);
}
@media (max-width: 768px) {
  .youtube-slider {
    max-width: 100%;
  }
}

.gray_arrows .slick-prev,
.gray_arrows .slick-next {
