.accordion_container {
  width: 100%;
  margin: auto;
}
.accordion_container .accordion-item {
  overflow: hidden;
}
.accordion_container .accordion-header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 18px 35px;
  background-color: #F5F5F5;
  cursor: pointer;
  transition: background 0.5s ease;
  border-bottom: 1px solid #F5682C;
}
.accordion_container .accordion-header:hover {
  background-color: #F5F5F5;
}
.accordion_container .accordion-header .accordion-arrow {
  width: 12px;
  height: 12px;
  border: solid #F5682C;
  border-width: 0 1px 1px 0;
  display: inline-block;
  padding: 5px;
  transform: rotate(45deg);
  transition: transform 0.3s ease;
}
.accordion_container .accordion-item.active .accordion-header {
  background-color: #fff;
}
.accordion_container .accordion-item.active .accordion-arrow {
  transform: rotate(-135deg);
}
.accordion_container .accordion-content {
  display: none;
  padding: 15px;
  animation: slideDown 0.3s ease-in-out;
  display: flex;
}
.accordion_container .accordion-content .column {
  flex: 1;
}
.accordion_container .accordion-content ul {
  list-style: none;
  padding: 0;
  width: 100%;
  column-count: 3;
  column-gap: 40px;
}
.accordion_container .accordion-content li {
  position: relative;
  padding-left: 20px;
  margin-bottom: 8px;
}
.accordion_container .accordion-content li::before {
  content: "";
  position: absolute;
  left: 0;
  top: 50%;
  width: 10px;
  height: 10px;
  background-color: #F5682C;
  border-radius: 50%;
  transform: translateY(-50%);
}
.accordion_container .accordion-item.active .accordion-content {
